.switch {
    margin-top: 4px
}

.switch-toggle {
    margin-left: -9999px;
    visibility: hidden
}

.switch-toggle + label {
    display: block;
    position: relative;
    cursor: pointer;
    outline: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none
}

input.switch-toggle-round + label:after, input.switch-toggle-round + label:before, input.switch-toggle-round-flat + label:after, input.switch-toggle-round-flat + label:before {
    position: absolute;
    content: "";
    display: block
}

input.switch-toggle-round + label {
    padding: 2px;
    width: 56px;
    height: 28px;
    background-color: #e5e5e5;
    border-radius: 28px
}

input.switch-toggle-round + label:after, input.switch-toggle-round + label:before {
    top: 1px;
    left: 1px;
    bottom: 1px
}

input.switch-toggle-round + label:before {
    right: 1px;
    background-color: #f1f1f1;
    border-radius: 28px;
    transition: background .4s
}

input.switch-toggle-round + label:after {
    width: 26px;
    background-color: #fff;
    border-radius: 100%;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12);
    transition: margin .4s
}

input.switch-toggle-round:checked + label:before {
    background-color: #66BB6A
}

input.switch-toggle-round:checked + label:after {
    margin-left: 28px
}

input.switch-toggle-round-flat + label {
    padding: 2px;
    width: 56px;
    height: 28px;
    background-color: #e5e5e5;
    border-radius: 28px;
    transition: background .4s
}

input.switch-toggle-round-flat + label:before {
    top: 2px;
    left: 2px;
    bottom: 2px;
    right: 2px;
    background-color: #fff;
    border-radius: 28px;
    transition: background .4s
}

input.switch-toggle-round-flat + label:after {
    top: 4px;
    left: 4px;
    bottom: 4px;
    width: 20px;
    border-radius: 20px;
    background-color: #e5e5e5;
    transition: margin .4s, background .4s
}

input.switch-toggle-round-flat:checked + label {
    background-color: #66BB6A
}

input.switch-toggle-round-flat:checked + label:after {
    margin-left: 28px;
    background-color: #66BB6A
}

